body {
  font-size: 1rem;
  color: $base-color;
  font-family: $body-font;
  h1 {
    @include font-size(4rem);
  }
  h2 {
    font-size: 3rem;
  }
  h3 {
    font-size: 2.25rem;
  }
  h4 {
    font-size: 1.875rem;
  }
  h5 {
    font-size: 1.5rem;
  }
  h6 {
    font-size: 1.25rem;
  }
  @media screen and (min-width: $tablet) {
    font-size: 1.25rem;
  }
  @media screen and (min-width: $desktop) {
    font-size: 1.5rem;
  }
}

div.wrapper {
  width: 100vw;
  display: flex;
  flex-direction: column;
