a {
  text-decoration: none;
  &:hover {
    color: $accent;
    cursor: pointer;
  }
}

.hero {
  height: 100vh;
  background-image: url('static/mastehead.jpg');
  background-size: cover;
  background-position: center;
  color: white;
  .navigation {
    padding: 1rem 2.5rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    .brand {
      // font-size: 1.3rem;
      font-family: $heading-font;
      letter-spacing: 0.3rem;
      font-style: italic;
    }
    img {
      margin-right: 0.4rem;
      height: 1.8rem;
      width: 1.8rem;
    }
  }
}

