@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600|Playfair+Display:400,400i&display=swap');

// fonts
$body-font: 'IBM Plex Sans', sans-serif;
$heading-font: 'Playfair Display', sans-serif;

// Sizing
$base-font-size: 16px;
$text-h6: 20px;
$text-h5: 24px;
$text-h4: 30px;
$text-h3: 36px;
$text-h2: 48px;
$text-h1: 64px;

// Color
$base-color: #333;
$gray: #eee;
$accent: #9c2532;
$pinterest: #bd081c;
$twitter: #1da1f2;
$instagram: #000;

// media
$tablet: 768px;
$desktop: 1024px;
